.WorkScheduleManage{
    width: 100%;
    height: 100%;
    /* border: solid 2px black; */
}

.periodcontents{
    display: flex;
    flex-direction: row;
    margin: 30px;
}

.periodTitle{
    margin-right: 10px;
    color: black;
    font-size: 20px;
    text-align: center;
    line-height: 40px;
}

.periodResult{
    width: 500px;
    height: 40px;
    color: black;
    background-color: #D9D9D9;
    border-radius: 20px;
    font-size: 20px;
    text-align: center;
    line-height: 40px;
}

.Modificationperiod{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.DatePicker{
    width: 150px;
    height: 40px;
}

.modificationBtn{
    margin-top: 300px;
    text-align: center;
}

.mofification_btn{
    color: white;
    width: 300px;
    height: 40px;
    background-color: #A87EFF;
    border-radius: 20px;
    border: none;
    font-size: 20px;
}